          @import "@/assets/core";
        

@import '@/assets/variables';

.content {
  margin-bottom: 25px;
}

.toast {
  background-color: var(--secondary-bg);
  padding: 10px;
  border-radius: 8px;
}

.description {
  margin-top: 25px;

  &::v-deep {
    p + p {
      margin-top: 5px;
    }
  }
}

.comparison {
  margin-bottom: 25px;

  &__table {
    width: 100%;

    th {
      border-bottom: 1px solid #dcd9d9;
      padding-bottom: 10px;
      font-weight: 600;
    }

    td {
      padding: 10px;
      vertical-align: middle;
      white-space: pre-line;
      line-height: 1.2;
    }

    td:first-child {
      width: 35%;

      @include tablet {
        width: auto;
      }
    }

    td:not(:last-child) {
      border-right: 1px solid #dcd9d9;
    }

    tr:not(:last-child) td {
      border-bottom: 1px solid #dcd9d9;
    }

    td:not(:first-child) {
      text-align: center;
    }

    td:first-child {
      @include tablet {
        width: 150px;
      }
    }
  }

  &__footnote {
    margin-top: 10px;
    font-size: 12px;
  }
}

.no-icon,
.yes-icon {
  font-size: 22px;
  vertical-align: middle;
}

.no-icon {
  color: var(--text-error);
}

.yes-icon {
  color: var(--text-success);
}
